@use '../../foundation/mixin' as m;
@use '../../foundation/variable' as v;

.p-footer {
	position: relative;
	background: #f3bf7c;
	overflow: hidden;

	.c-sun,
	.c-sky,
	.c-sea,
	.c-fin,
	.c-wave,
	.c-wave--color,
	.c-copyright {
		position: absolute;
	}

	.c-sun {
		left: 50%;
		transform: translateX(-50%);
		z-index: 1;
	}

	.c-sky {
		top: 0;
		z-index: 10;
	}

	.c-sea {
		top: 45%;
		z-index: 20;
	}

	.c-wave {
		top: 36%;
		z-index: 10;

		&--color {
			top: 56%;
			left: -5%;
			z-index: 10;
			@include m.breakpoint(moon-mini) {
				top: 55%;
			}
		}
	}

	.c-fin {
		top: 70%;
		z-index: 30;
	}
}

.p-footer--night {
	@extend .p-footer;
	background: linear-gradient(to bottom, #2e2b42 50%, #ede9fe);

	.c-sea {
		&::after {
			background: radial-gradient(
				at 50% 0,
				rgba(0, 200, 255, 0.8),
				transparent
			);
		}
	}

	.c-wave {
		&--color {
			background: #ad9afe;
			// background: #191236;
			filter: blur(30px);
		}
	}

	.c-sky,
	.c-sun {
		display: none;
	}
}
