@mixin breakpoint($point) {
	@if $point == iphone-normal {
		@media (min-width: 374px) {
			@content;
		}
	}
	@if $point == ipad-mini {
		@media (min-width: 438px) {
			@content;
		}
	}
	@if $point == moon-mini {
		@media (min-width: 570px) {
			@content;
		}
	}
	@if $point == tablet {
		@media (min-width: 600px) {
			@content;
		}
	}
	@if $point == header-large {
		@media (min-width: 740px) {
			@content;
		}
	}
	@if $point == back-to-moon-large {
		@media (min-width: 751px) {
			@content;
		}
	}
	@if $point == contact-col {
		@media (min-width: 800px) {
			@content;
		}
	}
	@if $point == topic-inner {
		@media (min-width: 960px) {
			@content;
		}
	}
	@if $point == wide {
		@media (min-width: 1120px) {
			@content;
		}
	}
}

// **
// line-heightの余白を取り除く
// **
@mixin lineHeightCrop-before($line-height) {
	&::before {
		display: block;
		width: 0;
		height: 0;
		margin-top: calc((1 - #{$line-height}) * 0.5em);
		content: '';
	}
}
@mixin lineHeightCrop-after($line-height) {
	&::after {
		display: block;
		width: 0;
		height: 0;
		margin-bottom: calc((1 - #{$line-height}) * 0.5em);
		content: '';
	}
}
