@keyframes animGravity {
	0% {
		transform: translateY(-26px);
		opacity: 0;
	}
	30%,
	80% {
		transform: translateY(0px);
		opacity: 1;
	}
	92%,
	100% {
		transform: translateY(-4px);
		opacity: 0;
	}
}

