@use '../../foundation/mixin' as m;
@use '../../foundation/variable' as v;

.single {
	.l-topic {
		margin-top: 0;
		@include m.breakpoint(moon-mini) {
			margin-top: 100px;
		}

		.p-svg-cosmos {
			&--ornament {
				width: 2%;
				left: 5%;
				@include m.breakpoint(moon-mini) {
					left: 15%;
					opacity: 0.3;
				}
			}
		}

		.l-topic__container {
			.c-title--section {
				text-align: left;
				text-indent: 0;
			}
			.c-post-date {
				font-size: 1rem;
				margin-bottom: 10px;
			}
			.wp-post-image {
				opacity: 0.7;

				&__container {
					margin-bottom: 40px;

					.fa-external-link-alt {
						position: absolute;
						bottom: 5px;
						left: 5px;
						font-size: 3rem;
						color: #9999;
						opacity: 0.8;
						z-index: 2;
					}

					.eye-catch-text {
						font-size: 6vw;
						margin-bottom: 0;
						@include m.breakpoint(moon-mini) {
							font-size: 3rem;
							line-height: 2;
						}
					}
				}
			}

			.c-falling-box {
				position: absolute;
				bottom: -10%;
				left: 45%;
			}
		}
	}
}

@keyframes title-after {
	100% {
		transform: translateX(0);
	}
}

// コンテンツ内枠
.single {
	.l-topic .l-topic__container,
	.p-related-contents__inner {
		position: relative;
		display: block;
		width: 100%;

		@include m.breakpoint(topic-inner) {
			width: v.$width--topic-inner;
		}
	}
}

// タグリスト
.single {
	.field-list {
		display: flex;
		flex-wrap: wrap;
		gap: 10px;
		padding-left: 0;

		li {
			position: relative;
			white-space: nowrap;
			list-style: none;
			margin: 0;

			&::after {
				position: absolute;
				content: '';
				display: block;
				width: 100%;
				height: 100%;
				top: 0;
				border-radius: 20px;
				opacity: 0.4;
			}

			.text {
				line-height: 2;
				padding: 0 10px;
			}
		}
	}
}

// 見出し
.single .l-topic,
.p-related-contents {
	h2 {
		position: relative;
		display: inline-block;
		font-size: 2rem;
		padding: 0 10px;
		overflow: hidden;

		&::before,
		&::after {
			position: absolute;
			top: 0;
			left: 0;
			display: block;
			content: '';
			width: 100%;
			height: 100%;
			border-radius: 5px;
			transform-origin: 0 0;
		}

		&::after {
			filter: blur(2px);
		}
	}
}

// 関連記事
.single {
	.p-related-contents {
		width: 100%;
		height: 100%;

		&__inner {
			min-height: 700px;
			padding: 0 20px;
			@include m.breakpoint(topic-inner) {
				padding: 0;
			}

			.p-svg-cosmos--crystal {
				position: absolute;
				top: -5%;
				left: 0;
			}

			.c-ball__container {
				position: absolute;
				top: 10%;
				left: 10%;
				width: 80%;
				height: 100%;

				.c-ball {
					&:nth-child(5) {
						left: 0;
						bottom: 20%;
					}
				}
			}
		}
	}
}
