@use "../../foundation/variable" as v;
@use "../../foundation/mixin" as m;

.p-jump-button {
  .c-read-more {
    width: 300px;
    height: 100px;
  
    .c-ball {
      width: 80px;
      height: 80px;
      right: 5%;
      bottom: 10%;
    }

    .text {
      left: 10%;
      bottom: 20%;
    }

    .c-circle-text {
      &:first-child {
        left: 25%;
      }
      &:last-child {
        left: 68%;
      }
      span {
        height: 57px;
      }
    }
  }
}