@use "../../foundation/variable" as v;
@use "../../foundation/mixin" as m;

.c-separator-cloud {
	position: relative;
	.st0 {
		animation: st-0 3s infinite alternate ease-in-out backwards;
	}
	.st1 {
		animation: st-1 4s infinite alternate ease-in-out backwards;
	}
	.st2 {
		animation: st-2 5s infinite alternate ease-in-out backwards;
	}

	.cloud-ball {
		transition: animation .5s;

		$count: 74;
		$delay: 0;
		@for $i from 1 through $count {
			&:nth-child(#{$i}) {
				animation-delay: -5s + $delay;
			}
			$delay: $delay + .1;
		}
	}

	&--bottom {
		margin-top: -5px;
		transform: rotate(180deg);
	}
}

@keyframes st-0 {
	0% {
		transform: translateY(-5px);
		opacity: 0;
	}
	90% {
		transform: translateY(-5px);
	}
	100% {
		transform: translateY(5px);
		opacity: 0.5;
	}
}
@keyframes st-1 {
	0% {
		transform: translateY(-5px);
		opacity: 0;
	}
	90% {
		transform: translateY(-5px);
	}
	100% {
		transform: translateY(5px);
		opacity: 0.3;
	}
}
@keyframes st-2 {
	0% {
		transform: translateY(-5px);
		opacity: 0.75;
	}
	90% {
		transform: translateY(-5px);
	}	
	100% {
		transform: translateY(5px);
		opacity: 0;
	}
}