// background: linear-gradient(-20deg, #ddd6f3 0%, #faaca8 100%, #faaca8 100%);


.c-cloud {
	&__container {
		position: absolute;
		top: -30%;
		width: 100%;
		overflow-x: hidden;
	}

	display: inline-block;
	width: 100px;
	height: 30px;
	border-radius: 50px 35px 35px 15px;
	background: rgb(145,133,132);
	filter: blur(10px);

	&::before,
	&::after {
		position: absolute;
		content: '';
		display: block;
		width: 40px;
		border-radius: 35px;
		background: rgb(145,133,132);
	}

	&::before {
		height: 45px;
		left: 12px;
		bottom: 0;
	}

	&::after {
		height: 50px;
		right: 16px;
		bottom: 0;
	}
}