.c-planetary-shadow {
  &__wrapper {
    width: 100vw;
    overflow: hidden;
  }

  position: absolute;
  border-radius: 100% / 100%;

  &--horizon {
    @extend .c-planetary-shadow;
    width: 160%;
    height: 44%;

    background: #038bff;
    filter: blur(30px);

    left: 50%;
    bottom: -34%;
    margin-left: -80%;

    &::before,
    &::after {
      content: " ";
      position: absolute;
      filter: blur(30px);
    }
    &:before {
      width: 81.25%;
      height: 70%;
      border-radius: 100% / 100%;
      background: #51afff;
      opacity: 0.6;
      margin-left: 9.375%;
    }
    &:after {
      width: 32%;
      height: 20%;
      border-radius: 650px / 350px;
      background: #b0daff;
      opacity: 0.5;
      margin-left: 34%;
    }
  }

  &--glow {
    @extend .c-planetary-shadow;
    width: 100%;
    height: 100%;
    background: #215496;
    filter: blur(200px);
    opacity: 0.7;
    top: -10%;
  }
}


.c-planetary-shadow--earth {
  @extend .c-planetary-shadow;  
	width: 200%;
	height: 70%;
	background: #06254e;
	background-position: 50% -36%;
	background-size: cover;
	filter: blur(2px);

	left: 50%;
	bottom: -60%;
	margin-left: -100%;

  &::before,
  &::after {
    content: " ";
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 100% / 100%;
  }

	&:before {
		box-shadow: inset 0px 0px 62px 20px rgba(60, 105, 138, 0.85);
	}

	&:after {
		// @include filter-gradient(#000000, #000000, horizontal)
		// @include background-image(linear-gradient(left, rgba(0,0,0,1) 0%,rgba(0,0,0,0) 50%,rgba(0,0,0,1) 100%))
		border-radius: 100% / 100%;
	}
}