@use '../../foundation/variable' as v;
@use '../../foundation/mixin' as m;

.c-title {
	$letterSpacing: 8px;
	width: 100%;
	height: 100%;
	position: relative;
	display: inline-block;
	text-align: center;
	font-size: v.$font-size--sec-ttl;
	font-weight: v.$font-weight--c-sec-ttl;
	letter-spacing: $letterSpacing;
	text-indent: $letterSpacing;

	&::before,
	&::after {
		position: absolute;
		content: '';
		bottom: -2%;
	}

	&::before {
		display: block;
		transform-origin: 0 0;
	}

	span {
		display: inline-block;
	}
}

.c-title {
	&--section,
	&--page,
	&--small {
		@extend .c-title;
	}

	&--section {
		color: #fff;
		text-shadow: 2px 2px 5px #fff;
	}

	&--page {
		font-size: 2rem;
		font-weight: 400;
		color: rgba(200, 200, 200, 0.5);
		background: linear-gradient(
			90deg,
			rgba(255, 255, 255, 1) 10%,
			rgba(200, 200, 200, 0.1)
		);
		background-clip: text;
		-webkit-background-clip: text;
		-webkit-text-fill-color: transparent;

		@include m.breakpoint(topic-inner) {
			font-size: 2.4rem;
		}

		&::before {
			display: none;
		}
	}

	&--small {
		color: #fff;
		text-shadow: 2px 2px 5px #111;
	}
}

.home {
	.c-title--section,
	.c-title--section::before {
		width: v.$width--linear-light--home;
	}
}

.single {
	.c-title {
		&--page {
			margin-top: 150px;
			@include m.breakpoint(moon-mini) {
				margin-top: 110px;
			}
		}
	}
}
