@use '../../foundation/mixin' as m;
@use '../../foundation/variable' as v;

.single-post {
	.p-svg-cosmos {
		width: 200%;
		@include m.breakpoint(moon-mini) {
			width: 100%;
		}
		&--earth {
			top: 15%;
			@include m.breakpoint(moon-mini) {
				top: 10%;
			}
		}
		&--ornament {
			top: 350px;
			z-index: 10;
			@include m.breakpoint(moon-mini) {
				top: 350px;
			}
		}
	}

	.l-topic {
		font-family: sans-serif;

		&__inner {
			position: relative;
			padding-bottom: 50px;
			margin: -5px 0;

			@include m.breakpoint(moon-mini) {
				margin: -10px 0;
			}
		}

		&__container {
			color: #000;
			margin-bottom: 0;

			li {
				list-style: inherit;
				margin-left: 40px;
				overflow-wrap: break-word;
			}

			.c-title--section {
				color: #555;
				text-shadow: 1px 1px 4px #666;
			}

			.c-pseudo--sec-ttl {
				&::before {
					background: v.$bg-linear-light-before--post-sec-ttl;
				}
			}
		}

		.c-separator-cloud__container,
		.c-separator-cloud--bottom {
			width: 200%;
			@include m.breakpoint(moon-mini) {
				width: 100%;
			}
		}
	}
}

.single-post {
	.field-list {
		li {
			&::after {
				background: linear-gradient(
					to right,
					rgb(0, 0, 0),
					rgba(255, 255, 255, 0.4) 0%,
					rgba(0, 0, 0, 0.2) 90%
				);
				border-right: 1px solid rgba(0, 0, 0, 0.4);
				border-bottom: 1px solid rgba(0, 0, 0, 0.2);
				box-shadow: inset 1px 1px 1px rgba(0, 0, 0, 0.2);
			}
			.text {
				font-size: 1rem;
				color: #222;
			}
		}
	}
}
