@use '../../foundation/mixin' as m;
.cstmreba {
	margin-bottom: 40px;

	.booklink {
		&-box {
			display: flex;
			flex-direction: column;
			align-items: center;
			gap: 20px;

			@include m.breakpoint(tablet) {
				justify-content: center;
				flex-direction: row;
				align-items: flex-start;
				gap: 40px;
				font-size: 1.6rem;
			}
		}

		&-image {
			width: 60%;
			height: 100%;

			@include m.breakpoint(tablet) {
				width: 20%;
			}

			a {
				> img {
					display: block;
					width: 100%;
					height: 100%;
				}
			}
			img {
				display: none;
			}
		}

		&-info {
			line-height: 1.5;
			font-size: 1.4rem;
		}

		&-powered-date {
			display: none;
		}
	}
}
